import axios from 'axios';

import {apiDomain} from '../../config';

import XStorage from '../utils/localStorage';

const A = axios.create({
  baseURL: process.env.NODE_ENV === 'production' ? 'http://tennis-noob.win:8080/' : 'http://localhost:8080/',
  timeout: 10000
});


/*
* 拦截请求
* 每次请求时进行拦截进行签名和基础参数添加
*/
A.interceptors.request.use(function (config) {
  // Do something with request data
  console.log('before send => ', config);
  // 处理请求参数，增加时间戳
  if (config.method.toUpperCase() == 'POST') {
    config.url = config.url + '?t=' + new Date().getTime();
    config.headers = {
      'Content-Type': 'application/json'
    };
  } else {
    config.params.t = new Date().getTime();
  }
  
  return config;

}, function (error) {
  // Do something with request error
  console.log('request error => ', error);
  return Promise.reject(error);

});

/*
* 拦截响应
* 每次响应返回时进行拦截进行处理
*/
axios.interceptors.response.use(function (response) {
  // Do something with response data
  //console.log('response success => ', response);
  if(!response.data.result){
    console.log(response.data.msg, response.data.code);
  }
  return response;
}, function (error) {
  // Do something with response error
  console.log('response error => ', error);
  return Promise.reject(error);
});





export default A;


