import A from '../A';

import config from '../../../config';


export function fetchUserRegist({username, password, nickname, role, sex}){

  //console.log(username, password, nickname, role, sex);
  
  return new Promise((resolve, reject) => {
    A.post('api/user/reg', {
      username: username,
      password: password,
      nickname: nickname,
      role: role,
      sex: sex
    })
    .then(response => {
      console.log(response);
      resolve(response.data);
    })
  })
}

export function fetchUserInfo(username){
  
  return new Promise((resolve, reject) => {
    A.get('api/user/info', {
      params: {
        username: username
      }
    })
    .then(response => {
      console.log(response);
      resolve(response.data);
    })
  })
}



