import Vue from 'vue';
import VueRouter from 'vue-router';
/*
* 页面
*/

//------------工具-------------
import Tools from '../views/tools/list';
import CourtFormat from '../views/tools/court-format';
import Draw from '../views/tools/draw';
//------------活动-------------
import Activites from '../views/activites/index';
//------------个人-------------
import User from '../views/user/index';

// 引用VueRouter
Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'hash',
  routes: [
    {
      path: '/tools',
      name: 'tools',
      component: Tools
    },
    {
      path: '/court-format',
      name: 'courtFormat',
      component: CourtFormat
    },
    {
      path: '/draw',
      name: 'draw',
      component: Draw
    },
    {
      path: '/activites',
      name: 'activites',
      component: Activites
    },
    {
      path: '/user',
      name: 'user',
      component: User
    },
    { 
      path: '*', 
      redirect: '/tools' 
    }
  ]
})

export default router



