
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
;
import courtTranslate from '../../utils/courtTranslate';
//import * as T from '../../store/types.js';

export default {

  activated(){

  },
  
  updated(){
          
  },
 
  components : {
    Group,
    Cell,
    XTextarea,
    XButton,
    Flexbox, 
    FlexboxItem
  },

  data() {
    return {
      inputValue: '',
      outputValue: '',
      fdata: null
    }
  },

  computed: {
    noCourt(){
      return !this.outputValue;
    }
  },

  methods: {
    // 重置场地信息
    handleResetInputValue(){
      this.inputValue = '';
      this.outputValue = '';
    },

    // 统计贡献信息
    handleCountContribution(){
      if(!this.inputValue){
        this.$vux.toast.show({
          type: 'warn',
          text: '未填写需要统计的场地信息'
        });
        return;
      }

      const covertedData = courtTranslate(this.inputValue, 'count');

      if (covertedData.result) {
        this.outputValue = covertedData.data;
      } else {
        this.$vux.toast.show({
          type: 'warn',
          text: covertedData.msg
        });
      }
    },

    // 进行场地格式化
    handleConvertValue(){
      if(!this.inputValue){
        this.$vux.toast.show({
          type: 'warn',
          text: '未填写需要格式化的场地信息'
        });
        return;
      }

      const covertedData = courtTranslate(this.inputValue, 'format');

      if (covertedData.result) {
        this.outputValue = covertedData.data;
      } else {
        this.$vux.toast.show({
          type: 'warn',
          text: covertedData.msg
        });
      }
    }
  }
  
}




