import * as common from '../../../api/common';
import * as T from '../../types.js';

export const FETCH_USER_REGIST = ({commit, dispatch, state}, params) => {
  return common.fetchUserRegist(params).then(data => {
    commit(T.FETCH_USER_REGIST, data);
    console.log('fetchUserRegist', data);
  });
}




