
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
;
import draw from '../../utils/draw';
//import * as T from '../../store/types.js';

export default {

  activated(){

  },
  
  updated(){
          
  },
 
  components : {
    Group,
    Cell,
    XTextarea,
    XInput,
    XButton,
    Flexbox, 
    FlexboxItem
  },

  data() {
    return {
      inputValue: '',
      groupSize: '',
      drawedData: {},
      hasDraw: false
    }
  },

  computed: {

    title(){
      let t = '参赛名单';

      return t;
    },
    
    outputValue(){
      let arr = this.inputValue.replace(/[\n\r\s]/g, '').split(/[,，]/);
      // console.log(arr);
      if(arr.length == 1 && !arr[0].trim()){
        return '';
      }else{
        if(this.hasDraw){
          let str = '';
          for(let k in this.drawedData){
            str += `===== 第 ${k} 组 =====\n`;
            str += this.drawedData[k].map((item, index) => {
              return `No.${index + 1} ${item}`;
            }).join('\n');
            str += '\n';
          }
          return str;
        }else{
          return arr.map((item, index) => {
            return `No.${index + 1} ${item}`;
          }).join('\n');
        }
      }
    }
  },

  methods: {
    handleResetAll(){
      this.inputValue = '';
      this.groupSize = '';
      this.hasDraw = false;
      this.$vux.toast.show({
        text: '已重置'
      });
    },

    handleDraw(){
      const arr = draw(this.inputValue.replace(/[\n\r\s]/g, '').split(/[,，]/), this.groupSize);
      if(arr.result){
        this.hasDraw = true;
        this.drawedData = arr.data;
        this.$vux.toast.show({
          text: '抽签成功'
        });
      }else{
        this.hasDraw = false;
        this.$vux.toast.show({
          type: 'warn',
          text: arr.msg
        });
      }
    }
  }
}



